@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&display=swap);
html, body
{
  margin: 0;
  padding: 0;
}

body
{
  background: rgb(36, 36, 36);
}

.app
{
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: calc(100vh - 10px);
  margin: 0;
  padding: 10px 0 0 0;

  font-family: 'Montserrat', sans-serif;
}

.page
{
  margin-bottom: 20px;
  flex: 1 0 auto;
}

.error-message 
{
  width: 100vw;
  margin-top: 15px;
  font-size: 30px;
  text-align: center;
}

.cards
{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100vw;
  height: 65vh;
  min-height: 500px;
  margin: 0;
  margin-top: 50px;  
}

@media screen and (max-width: 1000px)
{
  .cards
  {
    flex-direction: column;
    justify-content: center;
    justify-content: space-between;
    height: 60vh;
  }
}
.header
{
  color: white;
  margin: 0;
  padding: 0;
  font-size: 50px;
  text-align: center;
}

@media screen and (max-width: 750px)
{
  .header
  {
    font-size: 6vw;
  }
}
.search-form
{
  display: flex;
  width: 35vw;
  min-width: 300px;
  height: 40px;
  margin: auto;
  margin-top: 15px;  
}

.search-bar
{
  background: rgb(255, 255, 255);
  height: 100%;
  font-size: 18px;
  margin: 0;
  padding: 0;
  padding-left: 7px;
  border: none;
  border-radius: 0;
  outline: none;
  flex: 1 1;
}

.search-bar::-webkit-input-placeholder
{
  font-size: 18px; /* Same as search-bar's font-size */
  font-style: italic;
}

.search-bar:-ms-input-placeholder
{
  font-size: 18px; /* Same as search-bar's font-size */
  font-style: italic;
}

.search-bar::-ms-input-placeholder
{
  font-size: 18px; /* Same as search-bar's font-size */
  font-style: italic;
}

.search-bar::placeholder
{
  font-size: 18px; /* Same as search-bar's font-size */
  font-style: italic;
}

.search-button
{
  background: rgb(184, 184, 184);
  color: black;
  width: 90px;
  height: 40px; /* Same as search-form's height */
  font-size: 18px;
  font-weight: 500;
  border: none;
  outline: none;
  cursor: pointer;
  transition: 0.2s;
}

.search-button:hover
{
  background: rgb(207, 207, 207);
}
.card 
{
  background: rgb(240, 240, 240);
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  width: 25%;
  height: 100%;
  border-radius: 50px;
  text-align: center;
}

.container
{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 120px;
  margin: auto 0;
}

.title
{
  margin: 0;
  padding: 0;
  font-size: 50px;
  font-weight: 700;  
}

.amount
{
  margin: 0;
  padding: 0;
  font-size: 40px;
  font-weight: 500;  
}

@media screen and (max-width: 1000px)
{
  .card
  {
    width: 90%;
    height: 22.5%;
    min-height: 150px;
  }

  .container
  {
    height: 85%;
  }
}

@media screen and (max-width: 600px)
{
  .title
  {
    font-size: 8vw; 
  }

  .amount
    {
    font-size: 7vw;
  }
}
footer
{
  background: rgb(240, 240, 240);
  width: calc(100vw - 10px);
  height: 30px;
  line-height: 30px; /* Same as height */
  padding: 10px 0 10px 10px;
  font-size: 24px;
  font-weight: 400;
  flex-shrink: 0;
}

footer a
{
  color: blue;
  text-decoration: none;
}

footer a:hover
{
  text-decoration: underline;
}

@media screen and (max-width: 1000px)
{
  footer
  {
    height: 65px;
  }
}

@media screen and (max-width: 510px)
{
  footer
  {
    font-size: 4vw;
  }
}
